/* Image */

	.image {
		border: 0;
		display: inline-block;
		position: relative;

		img {
			display: block;
		}

		&.left,
		&.right {
			max-width: 40%;

			img {
				width: 100%;
			}
		}

		&.left {
			float: left;
			padding: 0 1.5em 1em 0;
			top: 0.25em;
		}

		&.right {
			float: right;
			padding: 0 0 1em 1.5em;
			top: 0.25em;
		}

		&.fit {
			display: block;
			margin: 0 0 _size(element-margin) 0;
			width: 100%;

			img {
				width: 100%;
			}
		}

		&.featured {
			width: 100%;
			display: block;
			margin: 0 0 _size(section-spacing) 0;
			border: 1px solid #ddd;
			border-radius: 4px;

			img {
				width: 100%;
			}

			@include breakpoint(small) {
				margin: 0 0 _size(section-spacing-small) 0;
			}
		}

		&.main {
			display: block;
			margin: 0 0 (_size(element-margin) * 1.5) 0;
			width: 100%;

			img {
				width: 100%;
			}
		}
	}
@media (max-width: 768px) {
	.image.left.image-icon {
		padding: 0;
		float: none;
    		padding-bottom: 32px;
  	}
}
